body{
    margin: 0;
}

.App{
    display: flex;
    width: 100lvw;
    max-width: 100%;
    margin: auto;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}


:root{
    --width:1110px;
}

@media screen and (min-width: 601px) and (max-width: 1139.98px){
    :root{
        --width:calc(100% - 40px);
    }
}


@media screen and (max-width: 600.98px){
    :root{
        --width:calc(100% - 20px);
    }
}