.InfoSection{
   display: flex;
   flex-wrap: wrap;
   justify-content: space-evenly;
   margin-top: 65px;
   gap: 60px 30px;
   width: var(--width);
}

.InfoSection div{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 60px 30px;
}

.InfoSection article{
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    height: 105px;
    width: 255px;
    background-color: white;
    border-radius: 15px;
    text-align: center;
}


.InfoSection p{
    position: relative;
    top:-50px; 
    color: #473E43;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px; 
}

.InfoSection span{
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0 0 77px;
    width: 77px;
    border-radius: 50%;
    background-color: white;

    position: relative;
    top:-25px;   
}

.InfoSection img{
    width: 44px;
    height: 44px;
}