.contactList{
    margin-top: 25px;
}

.contact{
    margin-top: 30px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    gap:15px;
}

.contact a{
    color: #0E0C0D;
    font-family: Montserrat;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    box-sizing: border-box;
    border-radius: 15px;
    height: 50px;
    border: 2px solid #E3ECF5;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 5 40px;
}

.contact a:last-child{
    flex: 2 200px; 
} 

.contactHeader{
    color: #0E0C0D;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-transform: uppercase;
}

