.loader{
    width: 10%px;
    height: 10px;
    border: 2px solid var(--yellow-color);
}

.line{
    width: 10%;
    height: 100%;
    background-color: var(--yellow-color);
    animation:move 3s linear 1s infinite alternate;
}

@keyframes move {
    from {
        transform: translateX(0%);
    }

    to {
        transform: translateX(900%);
    }
}
