.social{
    display: grid;
    grid-template-columns: 1f;
    grid-row: 1fr;
    gap:10px;
    color: var(--link-color);
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 28px;
}

.social header{
    width: 100%;
}

.social main{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 70px;
}

.social a{
    max-width: 200px;
    opacity: 0.7;
}

.doubleRow{
    align-self: baseline;
    grid-column: 3/4;
    grid-row: 1/3;
}