.button{
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 14px;
    line-height: 28px;
    background-color: var(--yellow-color);
    border-radius: 8px;
    border: 0;
    box-sizing: content-box;
    padding: 4px 25px 3px 23px;
    color: var(--light-color);
}

.button:hover{
    color: var(--active-gray);
}