.Menu{
    display: flex;
    flex-direction: column;
    width: var(--width);
}

.menuListTitle{
    color:var(--yellow-color);
    font-family: Montserrat;
    font-size: 32px;
    font-weight: 800;
}


.menuListMain{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(252px, 1fr));
    gap: 50px 30px;
    margin-top: 25px;
}

.menuItem{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.menuItemMain{
    display: flex;
    flex-direction: column;
    width: 100%;
}

.menuItemImg{
    object-fit: contain;
}

.menuItemName {
    margin: 0;
    line-height: 28px;
    font-family: 'Montserrat';
    font-weight: 800;
    margin-top: 8px;
    color: var(--light-gray-color);
}

.menuItemDirection{
    margin: 0;
    margin-top: 14px;
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 13px;
    line-height: 19px;
    color:var(--gray-color);
}

.menuItemFooter{
    display: flex;
    width: 100%;
    margin-top: 25px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.menuItemCost{
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 22px;
    line-height: 19px;
    color:var(--dark-color);
}


@media  screen and (max-width:858px) {
    .menuItemMain{
        flex-direction: row;
    }

    .menuItemImg{
        width: 122px;
    }
    
    .menuItemName{
        font-size: 13px;
    }

    .menuItemDirection{
        font-size: 11px;
    }
    
}
