.PaymentAndDelivery{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #E3ECF5;
    width: 100%;
    padding: 55px 0 75px 0;

}

.PaymentAndDelivery header{
    color: var(--yellow-color);
    text-align: center;
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 800;
}

.PaymentAndDelivery button{
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    margin-top: 30px;
    border-radius: 15px;
    border: 0;
    background-image: url("../../img/map.png");
    width: var(--width);
    background-size:cover;
    text-align: center;
    padding: 150px 0 150px 0;
    cursor: pointer;
    color: var(--dark-gray-color);
    font-family: Montserrat;
    box-shadow: 0px 0px 10px -4px black;
}
