.Slider{
    width: 100%;
    overflow: hidden;
}

.Slider article{
    display: flex;
    position: relative;
    gap: 30px;
    align-items: center;
    width: max-content;
    left: calc((100% - 1110px)/2);
    transition: 1s;
}

.Slider button {
    position: relative;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: var(--yellow-color);
    border: none;
    z-index: 1;

    font-family: Montserrat;
    font-size: 12px;
    font-style: normal;
    font-weight: 900;
    text-align: center;
    line-height: 10px;
    
}

.Slider button:first-child{
    left: calc(50% - 580px);
    top: 155px;
}

.Slider button:nth-child(2){
    left: calc(50% + 540px);
    top: 155px;
}

.element{
    border-radius: 14px;
}

.element img{
    width: 100%;
    height: 100%;
    border-radius: 14px;
    object-fit: cover;
}

.unactive{
    width: 475px;
    height: 275px;
    opacity: 60%;
    transition: 1s;
}

.active{
    width: 540px;
    height: 310px;
    transition: 1s;
}


@media screen and (max-width: 769px){
    .Slider button{
        display: none;
        padding: 0 10px 0 10px;
    }

    .Slider {
        overflow: scroll;
    }

    .Slider article{
        position: relative;
        left: 0;
        margin: 0 10px 0 10px;
        gap: 10px
    }
    .active,
    .unactive{
        width: 350px;
        height: 200px;
        opacity: 100%;
    }
}

