.mainHeader{
    display: flex;
    flex-direction: row;
    padding-top: 15px;
    justify-content: space-between;
    align-items: center;
    width: var(--width);
    color: var(--dark-color);
}

.left{
    display: flex;
    flex-direction: row;
}
.mid{
    display: flex;
    flex-direction: column;
    margin-left: 52px;
}

.name{
    font-family: 'Montserrat';
    font-size: 17px;
    font-style: normal;
    font-weight: 600;
}

.city{
    color: var(--yellow-color);
    font-family: 'Montserrat';
    font-size: 17px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-left: 8px;
}

.down{
    font-family: 'Montserrat';
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    padding-top: 6px;
    display: flex;
    align-items: center;
}

.up{
    padding-top: 7px;
}

.deliveryName{
    padding-left: 7px;
}

.circle{
    position: relative;
    width: 4px;
    height: 4px;
    background-color: var(--pink-r ed-color);
    border-radius: 50%;
    margin-left: 8px;
    top: 1px;
    left: 1px;
}

.deliveryRate{
    margin-left: 8px;
}

.spanTime{
    margin-left: 20px;
}

.time{
    margin-left: 8px;
}

.star{
    color: var(--yellow-color);
    font-size: 16px;
    margin-left: 5px;
}

img{
    object-fit: none;
}

@media  (max-width: 1200px) {
    .mid{ 
        display: none;
    }
}