.modal{
  position: fixed;
  top:0;
  bottom:0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalContent{
    padding: 25px;
    border-radius: 15px;
    min-width: 250px;
    background-color: var(--light-color);
}

